import {AfterViewInit, Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import {JsonEditorComponent, JsonEditorOptions} from '@maaxgr/ang-jsoneditor';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ILocalDatabaseShortInfo, ILocalDatabasesOverview} from '../../../_interfaces';
import {BackendConnectionService} from '../../../_services/backend-connection.service';
import {MatSnackBar} from '@angular/material/snack-bar';
import * as apiTemplateFile from '../../../../assets/templates/api_template.json';

@Component({
  selector: 'app-api-create-form',
  templateUrl: './api-create-form.component.html',
  styleUrls: ['./api-create-form.component.scss']
})
export class ApiCreateFormComponent implements OnInit {

  apiConfiguration: any;
  apiLabel: string;
  apiTemplate: any;

  @ViewChild(JsonEditorComponent)
  configEditor: JsonEditorComponent;
  public editorOptionsConfig: JsonEditorOptions;
  public editorOptionsReturnTemplate: JsonEditorOptions;

  constructor(private connection: BackendConnectionService, private _snackBar: MatSnackBar) {}

  ngOnInit(): void {
    // Init jsonEdeitor for configuration file
    this.editorOptionsConfig = new JsonEditorOptions();
    this.editorOptionsConfig.mode = 'code';
    this.editorOptionsConfig.modes = ['code'];
    this.editorOptionsReturnTemplate = new JsonEditorOptions();
    this.editorOptionsReturnTemplate.mode = 'text';
    this.editorOptionsReturnTemplate.modes = ['text'];

    this.apiTemplate = (apiTemplateFile as any).default;
    this.apiLabel = '';
    this.configEditor.data = this.apiTemplate;
  }

  onApiNameChange(): void {
    this.apiTemplate = this.configEditor.get() as any;
    this.apiTemplate.label = this.apiLabel;
    this.configEditor.data = this.apiTemplate;
    this.apiConfiguration = this.configEditor.get();
  }

  onJSONChange(): void {
    this.apiTemplate = this.configEditor.get() as any;
  }

  save(): void {
    const config = this.configEditor.get();
    this.connection.addNewApi(config).toPromise().then(
        () => {
          this.openSnackBar('Saved');
        },
        (error) => {
          this.openSnackBar('Unable to add new service');
        }
    );
  }

  openSnackBar(message: string) {
    this._snackBar.open(message, '', {duration: 2000});
  }

}
