import {Injectable} from '@angular/core';
import {settings} from '../../assets/settings';

@Injectable({
    providedIn: 'root'
})
export class PathFormatterService {

    private apiPathFull = true;
    private apiWithWildcard = false;
    private relationPathFull = true;

    constructor() {
    }

    public formatApiPath(path: string): string {
        const context: string[] = path.match(settings.regexApiSearch);
        if (context == null){
            return path;
        }
        for (let i = 0; i < context.length; i++) {
            context[i] = context[i].replace(settings.regexApiReplace, '');
        }
        // remove wildcards
        for (let i = 0; i < context.length; i++) {
            context[i] = context[i].replace(settings.regexApiReplaceWildcard, '');
        }
        return context.join();
    }

    public formatRelationPath(path: string): string {
        const context: string[] = path.match(settings.regexRelSearch);
        if (context == null){
            return path;
        }
        for (let i = 0; i < context.length; i++) {
            context[i] = context[i].replace(settings.regexRelReplace, '');
        }
        return context.join();
    }

    public joinRelPaths(paths: string[]): string {
        return paths.join(settings.joinSymbol);
    }
}
